<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc">
          <star :size='36' :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">{{seller.sellCount}}</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>元
            </div>
          </li>
        </ul>
        <div class="favorite" @click.stop.prevent="toggleFavorite">
          <span class="icon-favorite" :class="{'active': favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item" v-for="item in seller.supports" >
            <span :class="classMap[item.type]" class="icon"></span>
            <span class="text">{{item.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" alt="" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title">商家信息</h1>
        <ul class="">
          <li class="info-item" v-for="info in seller.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from './../star/star.vue'
import split from './../split/split.vue'
import BScroll from 'better-scroll'
import {saveToLocal, loadFromLocal} from './../../common/js/store.js'

export default {
  name: 'seller',
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    star,
    split
  },
  data () {
    return {
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  computed: {
    favoriteText () {
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  mounted () {
    // console.log('mounted')
    this.$nextTick(() => {
      this._initScroll()
      this._initPic()
    })
  },
  watch: {
    seller: function () {
      // console.log('change')
      this.$nextTick(() => {
        this._initScroll()
        this._initPic()
      })
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  },
  methods: {
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    _initPic () {
      if (this.seller.pics) {
        let picWidth = 120
        let margin = 6
        let width = (picWidth + margin) * this.seller.pics.length - margin
        this.$refs.picList.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            })
          } else {
            this.picScroll.refresh()
          }
        })
      }
    },
    toggleFavorite (event) {
      console.log('click')
      if (!event._constructed) {
        return
      }
      this.favorite = !this.favorite
      console.log(this.favorite)
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "./../../common/stylus/mixin.styl"
  .seller
    height:100%
    width:100%
    overflow:hidden
    .overview
      position: relative
      padding:18px
      .title
        margin-bottom:8px
        line-height:14px
        color:rgb(7, 17, 27)
        font-size:14px
      .desc
        padding-bottom: 18px
        border-1px(rgba(7, 17, 27, .1))
        font-size:0
        .star
          display:inline-block
          vertical-align:top
          margin-right:8px
        .text
          vertical-align:top
          margin-right:12px
          font-size: 10px
          color:rgb(77, 85, 93)
          line-height: 18px
      .remark
        display:flex
        padding:18px
        .block
          flex:1
          text-align:center
          border-right:1px solid rgba(7, 17, 27, .1)
          &:last-child
            border: none
          h2
            font-size: 10px
            color: rgb(147, 153, 159)
            line-height: 10px
            margin-bottom: 4px
          .content
            font-size:10px
            color: rgb(7, 17, 27)
            line-height:24px
            .stress
              font-size: 24px
      .favorite
        position: absolute
        right: 18px
        top:18px
        text-align:center
        min-width:30px
        .icon-favorite
          display:block
          margin-bottom: 2px
          color: #d4d6d9
          line-height: 24px
          font-size: 24px
          &.active
            color: rgb(240, 20, 20)
        .text
          font-size: 10px
          color:rgb(77, 85, 93)
          line-height:10px
    .bulletin
      padding: 18px 18px 0 18px
      .title
        margin-bottom:8px
        line-height:14px
        color:rgb(7, 17, 27)
        font-size:14px
      .content-wrapper
        padding: 0 12px 16px 12px
        border-1px(rgba(7, 17, 27, .1))
        .content
          font-size: 12px
          font-weight: 200
          color: rgb(240, 20, 20)
          line-height:24px
      .supports
        .support-item
          padding: 16px 12px
          font-size: 0
          border-1px(rgba(7, 17, 27, .1))
          &:last-child
            border-none()
          .icon
            display: inline-block
            vertical-align: top
            width: 12px
            height: 12px
            margin-right: 4px
            background-size:12px 12px
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_4')
            &.discount
              bg-image('discount_4')
            &.guarantee
              bg-image('guarantee_4')
            &.invoice
              bg-image('invoice_4')
            &.special
              bg-image('special_4')
          .text
            font-size: 12px
            line-height: 16px
            color: rgb(7, 17, 27)
            vertical-align:top
    .pics
      padding:18px
      .title
        margin-bottom: 12px
        line-height: 14px
        color: rgb(7, 17, 27)
        font-size: 14px
      .pic-wrapper
        width: 100%
        overflow:hidden
        .pic-list
          font-size: 0
          white-space:nowrap
          .pic-item
            display:inline-block
            margin-right:6px
            width:120px
            height:90px
            &:last-child
              margin-right:0
    .info
      padding: 18px 18px 0 18px
      .title
        padding-bottom: 12px
        line-height: 14px
        border-1px(rgba(7, 17, 27, .1))
        color: rgb(7, 17, 27)
        font-size: 14px
      .info-item
        padding: 16px 12px
        font-size:12px
        font-weight:200
        color: rgb(7, 17, 27)
        line-height: 16px
        border-1px(rgba(7, 17, 27, .1))
        &:last-child
          border-none()
</style>



